.img_adj {
  position: fixed;
  z-index: -1;
  opacity: 0.65;
}

.flex_adj {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* justify-content: center; */
  -ms-flex-align: center;
      align-items: center;
  /* position: fixed; */
  margin-top: 100px;
}

p {
  font-size: 20px;
}

.anim {
  -webkit-animation: anim 1s ease-out 0s 1;
          animation: anim 1s ease-out 0s 1;
}

@-webkit-keyframes anim {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes anim {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.mar-adjust {
  margin-top: 20px;
  margin-bottom: 20px;
}



